.backdrop{
    background:  #313131;
    width: 100%;
    height: 100%;
    opacity: .3;
    background-size: cover;
    position: cover;
    top: 0;
    left: 0;
   overflow: hidden;

  }

  
.backdrop:hover { 
    background:transparent;
       display: none;
    }



 .midimg{
    display: inline-block;
    position: relative;
  }
  .midimg:after {
    content: '';
    position: absolute;
    top: 0;
     right: 0;
    bottom: 0; left: 0;
    background:  #313131;
    /* width: inherit;
    height: inherit; */
    opacity: .3;
    /* background: url(http://placehold.it/20x20/1) repeat; */
 }

 .midimgcontainer{
    max-width:1700px ;
    max-height: 42vh;
}


 .componentcover{
    display: inline-block;
    position: relative;
  }
  .componentcover:after {
    content: '';
    position: absolute;
    top: 0;
     right: 0;
    bottom: 0; left: 0;
    background:  #313131;
    opacity: .2;
    z-index: 1;
 } 

 

 
.iframe-container{
    position: relative;
    width: 100%;
    padding-bottom: 56.25%; 
    height: 0;
  }
  .iframe-container iframe{
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .zindex{
    z-index: 4 !important;
  }